body {
  background-color: hsl(0, 0%, 97%);
  font: 20px "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
}
#title {
  text-align: center;
  font-size: 26px;
}
#recipe-list {
  border: 3px solid #0084cd;
  overflow: scroll;
  height: 20vh;
  border-radius: 12px;
  width: 75vw;
  margin: auto;
}
#recipeDisplay {
  border: 3px solid #0084cd;
  border-radius: 12px;
  overflow: scroll;
  height: 70vh;
  width: 75vw;
  margin: auto;
  margin-top: 10px;
}

#dialog {
  text-align: center;
  position: fixed;
  width: 500px;
  min-width: 300px;
  top: 50%;
  left: 50%;
  margin-left: -250px;
  margin-top: -200px;
  box-shadow: 3px 3px 10px black;
  border-radius: 15px;
  background: #bfc0bf;
  z-index: 1000;
  .input-title {
    margin: 10px;
  }
  textarea {
    margin-bottom: 10px;
    background: #556670;
    min-width: 300px;
    min-height: 60px;
    resize: vertical;
    color: darken($titles, 10%);
    font-weight: bold;
    font-size: 13px;
    overflow: auto;
    &:focus {
      box-shadow: 0 0 5px $background;
      outline: 1px solid $background;
      border: 1px solid $background;
    }
    &:nth-of-type(3) {
      min-height: 100px;
    }
    &:first-of-type {
      resize: none;
      min-height: 15px !important;
    }
  }
}
#recipe-title {
  border: 0.25px solid #0084cd;
  padding: 4px;
}

#show-recipe-title {
  border-bottom: 0.25px solid #0084cd;
  margin-top: 2px;
  margin-bottom: 8px;
  font-size: 24px;
  padding-bottom: 4px;
  padding-left: 4px;
}
